.content {
  grid-area: c;
  padding: 10px;
  background-color: lightblue;
}

.postHeader {
  margin: 10px 0;
  color: #000;
  font-size: 1.2em;
  font-weight: 500;
}
.addPost {
  display: grid;
  grid-template-columns: 9fr 3fr;
  align-content: center;
  margin-top: 20px;
}
.addPost button {
  margin-left: 10px;
  padding: 0 5px;
  cursor: pointer;
}
